import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of, tap } from 'rxjs';
import { Task } from '../models/task';
import { GroupedTask } from '../models/grouped-task';
import { TaskStatusDetail } from '../models/task-status-detail';
import { TasksCacheService } from '../cache-services/tasks-cache.service';

const API_BASE_URL = 'https://localhost:7036';

@Injectable({
  providedIn: 'root',
})
export class TasksService {
  constructor(private httpClient: HttpClient, private cacheService: TasksCacheService) {}

  getTasks(): Observable<GroupedTask[]> {
    const cachedData = this.cacheService.get('cachedData');
    if (cachedData) {
      return of(cachedData);
    } else {
      return this.httpClient.get<GroupedTask[]>(`${API_BASE_URL}/api/tasks`, { responseType: 'json' }).pipe(
        tap((data) => {
          this.cacheService.set('cachedData', data);
        })
      );
    }
  }

  getTaskByTaskID(taskID: number): Observable<Task> {
    const cachedData = this.cacheService.get('cachedData');
    if (cachedData) {
      return of(cachedData.flatMap((el: any) => el.tasks).filter((el: any) => el.taskID == taskID)[0]);
    } else {
      return this.httpClient.get<Task>(`${API_BASE_URL}/api/tasks/searchbytaskid/${taskID}`, { responseType: 'json' });
    }
  }

  insertTask(newTask: Task): Observable<Task> {
    return this.httpClient.post<Task>(`${API_BASE_URL}/api/createtask`, newTask, { responseType: 'json' });
  }

  updateTaskStatus(taskStatusDetail: TaskStatusDetail): Observable<TaskStatusDetail> {
    return this.httpClient.put<TaskStatusDetail>(`${API_BASE_URL}/api/updatetaskstatus`, taskStatusDetail, {
      responseType: 'json',
    });
  }

  editTask(editTask: Task): Observable<Task> {
    return this.httpClient.put<Task>(`${API_BASE_URL}/api/edittask`, editTask, { responseType: 'json' });
  }

  deleteTask(taskID: number): Observable<string> {
    return this.httpClient.delete<string>(`${API_BASE_URL}/api/deletetask/${taskID}`);
  }
}
