import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Observable, take } from 'rxjs';
import { Project } from '../../../models/project';
import { TaskPriority } from '../../../models/task-priority';
import { ProjectsService } from '../../../services/projects.service';
import { TaskPrioritiesService } from '../../../services/task-priorities.service';
import { TasksService } from '../../../services/tasks.service';
import { TasksCacheService } from 'src/app/cache-services/tasks-cache.service';
import { LoginService } from '../../../services/login.service';

@Component({
  selector: 'app-create-task',
  templateUrl: './create-task.component.html',
  styleUrls: ['./create-task.component.scss'],
})
export class CreateTaskComponent implements OnInit {
  newTaskForm: FormGroup | any;
  projects: Observable<Project[]>;
  employees: Observable<any>;
  taskPriorities: Observable<TaskPriority[]>;

  constructor(
    private tasksService: TasksService,
    private router: Router,
    private projectsService: ProjectsService,
    private taskPrioritiesService: TaskPrioritiesService,
    private loginService: LoginService,
    private cacheService: TasksCacheService
  ) {}

  ngOnInit(): void {
    this.newTaskForm = new FormGroup({
      taskID: new FormControl(0),
      taskName: new FormControl(null, [Validators.required, Validators.maxLength(100)]),
      description: new FormControl(null, [Validators.maxLength(200)]),
      projectID: new FormControl(null, [Validators.required]),
      assignedTo: new FormControl(null, [Validators.required]),
      taskPriorityID: new FormControl(null, [Validators.required]),
    });

    this.projects = this.projectsService.getProjects();
    this.employees = this.loginService.getEmployees();
    this.taskPriorities = this.taskPrioritiesService.getTaskPriorities();
  }

  onCreateTaskClick() {
    this.newTaskForm['submitted'] = true;

    if (this.newTaskForm.valid) {
      this.tasksService
        .insertTask(this.newTaskForm.value)
        .pipe(take(1))
        .subscribe({
          next: () => {
            this.cacheService.clear();
            this.router.navigate(['/employee', 'tasks']);
          },
          error: (error) => {
            console.log(error);
          },
        });
    } else {
      console.log(this.newTaskForm.errors);
    }
  }
}
