import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { TasksService } from '../../../services/tasks.service';
import { LoginService } from '../../../services/login.service';
import { TasksCacheService } from '../../../cache-services/tasks-cache.service';

@Component({
  selector: 'app-tasks',
  templateUrl: './tasks.component.html',
  styleUrls: ['./tasks.component.scss'],
})
export class TasksComponent implements OnInit, OnDestroy {
  taskGroups: any[];

  taskID: number;
  taskName: string;

  subscription: Subscription;

  constructor(
    private tasksService: TasksService,
    public loginService: LoginService,
    private cacheService: TasksCacheService
  ) {}

  ngOnInit() {
    this.subscription = this.tasksService.getTasks().subscribe((response) => {
      this.taskGroups = response;
    });
  }

  getTaskGroupBgCssClass(taskStatusName: string): string {
    let className;
    switch (taskStatusName) {
      case 'Holding':
        className = 'bg-secondary text-white';
        break;
      case 'Prioritized':
        className = 'bg-primary text-white';
        break;
      case 'Started':
        className = 'bg-info text-white';
        break;
      case 'Finished':
        className = 'bg-success text-white';
        break;
      case 'Reverted':
        className = 'bg-danger text-white';
        break;
      default:
        className = 'bg-warning text-white';
    }
    return className;
  }

  getTaskPriorityBadgeCssClass(taskPriorityName: string): string {
    let className;
    switch (taskPriorityName) {
      case 'Urgent':
        className = 'badge-danger';
        break;
      case 'Normal':
        className = 'badge-primary';
        break;
      case 'Below normal':
        className = 'badge-info';
        break;
      case 'Low':
        className = 'badge-secondary';
        break;
      default:
        className = 'badge-warning';
    }
    return className;
  }

  getTaskGroupTextCssClass(taskStatusName: string): string {
    let className;
    switch (taskStatusName) {
      case 'Holding':
        className = 'text-secondary';
        break;
      case 'Prioritized':
        className = 'text-primary';
        break;
      case 'Started':
        className = 'text-info';
        break;
      case 'Finished':
        className = 'text-success';
        break;
      case 'Reverted':
        className = 'text-danger';
        break;
      default:
        className = 'text-warning';
    }
    return className;
  }

  onDelete(taskID: number, taskName: string) {
    this.taskID = taskID;
    this.taskName = taskName;
  }

  onConfirmDelete(taskID: number) {
    this.tasksService.deleteTask(taskID).subscribe({
      next: () => {
        this.cacheService.clear();
        this.tasksService.getTasks().subscribe((response) => {
          this.taskGroups = response;
        });
      },
      error: (error) => {
        console.log(error);
      },
    });
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
