import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable, take } from 'rxjs';
import { Project } from '../../../models/project';
import { Task } from '../../../models/task';
import { TaskPriority } from '../../../models/task-priority';
import { ProjectsService } from '../../../services/projects.service';
import { TasksService } from '../../../services/tasks.service';
import { TaskPrioritiesService } from '../../../services/task-priorities.service';
import { LoginService } from '../../../services/login.service';
import { TasksCacheService } from '../../../cache-services/tasks-cache.service';

@Component({
  selector: 'app-edit-task',
  templateUrl: './edit-task.component.html',
  styleUrls: ['./edit-task.component.scss'],
})
export class EditTaskComponent implements OnInit {
  editTaskForm: FormGroup | any;
  projects: Observable<Project[]>;
  employees: Observable<any>;
  taskPriorities: Observable<TaskPriority[]>;
  task = {} as Task;
  taskID: number;

  constructor(
    private tasksService: TasksService,
    private router: Router,
    private projectsService: ProjectsService,
    private taskPrioritiesService: TaskPrioritiesService,
    private loginService: LoginService,
    private activatedRoute: ActivatedRoute,
    private cacheService: TasksCacheService
  ) {}

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((params) => {
      this.taskID = params['taskid'];
    });

    this.tasksService
      .getTaskByTaskID(this.taskID)
      .pipe(take(1))
      .subscribe((response) => {
        this.task = response;
      });

    this.editTaskForm = new FormGroup({
      taskID: new FormControl(this.taskID),
      taskName: new FormControl(this.task.taskName, [Validators.required, Validators.maxLength(100)]),
      description: new FormControl(this.task.description, [Validators.maxLength(200)]),
      projectID: new FormControl(this.task.projectID, [Validators.required]),
      assignedTo: new FormControl(this.task.assignedTo, [Validators.required]),
      taskPriorityID: new FormControl(this.task.taskPriorityID, [Validators.required]),
    });

    this.projects = this.projectsService.getProjects();
    this.employees = this.loginService.getEmployees();
    this.taskPriorities = this.taskPrioritiesService.getTaskPriorities();
  }

  onEditTaskClick() {
    this.editTaskForm['submitted'] = true;

    if (this.editTaskForm.valid) {
      this.tasksService.editTask(this.editTaskForm.value).subscribe({
        next: () => {
          this.cacheService.clear();
          this.router.navigate(['/employee', 'tasks']);
        },
        error: (error) => {
          console.log(error);
        },
      });
    } else {
      console.log(this.editTaskForm.errors);
    }
  }
}
