import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable, take } from 'rxjs';
import { Task } from '../../../models/task';
import { TaskStatus } from '../../../models/task-status';
import { TaskStatusDetail } from '../../../models/task-status-detail';
import { TaskStatusesService } from '../../../services/task-statuses.service';
import { TasksService } from '../../../services/tasks.service';
import { TasksCacheService } from '../../../cache-services/tasks-cache.service';

@Component({
  selector: 'app-update-task-status',
  templateUrl: './update-task-status.component.html',
  styleUrls: ['./update-task-status.component.scss'],
})
export class UpdateTaskStatusComponent implements OnInit {
  taskID: number;
  currentTask = {} as Task;
  currentTaskStatusDetail = {} as TaskStatusDetail;
  editTaskStatusForm: FormGroup | any;
  taskStatuses: Observable<TaskStatus[]>;

  constructor(
    private tasksService: TasksService,
    private router: Router,
    private taskStatusesService: TaskStatusesService,
    private activatedRoute: ActivatedRoute,
    private cacheService: TasksCacheService
  ) {}

  ngOnInit() {
    this.activatedRoute.params.pipe(take(1)).subscribe((params) => {
      this.taskID = params['taskid'];
    });

    this.editTaskStatusForm = new FormGroup({
      taskStatusDetailID: new FormControl(0),
      taskID: new FormControl(null),
      taskStatusID: new FormControl(null, [Validators.required]),
      description: new FormControl(null, [Validators.maxLength(200)]),
    });

    this.taskStatuses = this.taskStatusesService.getTaskStatuses();

    this.tasksService
      .getTaskByTaskID(this.taskID)
      .pipe(take(1))
      .subscribe((task: Task) => {
        this.currentTask = task;

        this.currentTaskStatusDetail.taskID = this.taskID;
        this.currentTaskStatusDetail.description = '';
        this.currentTaskStatusDetail.taskStatusID = 0;
        this.currentTaskStatusDetail.taskStatusDetailID = 0;
        this.editTaskStatusForm.patchValue(this.currentTaskStatusDetail);
      });
  }

  onUpdateTaskStatusClick() {
    this.editTaskStatusForm['submitted'] = true;

    if (this.editTaskStatusForm.valid) {
      this.tasksService.updateTaskStatus(this.editTaskStatusForm.value).subscribe({
        next: () => {
          this.cacheService.clear();
          this.router.navigate(['/employee', 'tasks']);
        },
        error: (error) => {
          console.log(error);
        },
      });
    } else {
      console.log(this.editTaskStatusForm.errors);
    }
  }
}
